window.$ = window.jQuery = function (selectorOrArray) {
  //‰∏ÄË°å‰ª£Á†ÅÊúâÂ§ö‰∏™Á≠âÂè∑Êó∂Ôºå‰ªéÂè≥ÂêëÂ∑¶ÊâßË°å
  let elements
  if (typeof selectorOrArray === 'string') {
    elements = document.querySelectorAll(selectorOrArray)
  } else if (selectorOrArray instanceof Array) {
    elements = selectorOrArray
  }

  return {
    addClass(className) {
      //jqueryÊé•Âèó‰∏Ä‰∏™ÈÄâÊã©Âô®ÔºàselectorÔºâÔºåÊ†πÊçÆËøô‰∏™ÈÄâÊã©Âô®Ëé∑Âèñ‰∏Ä‰∫õÂÖÉÁ¥†ÔºàelementsÔºâÔºå‰ΩÜÊòØËøîÂõû‰∏Ä‰∏™ÂØπË±°ÔºåËøô‰∏™ÂØπË±°Êúâ‰∏Ä‰∏™ÊñπÊ≥ïÂèØ‰ª•Êìç‰ΩúËøô‰∏™ÂÖÉÁ¥†ÔºàËøôÈáåÊåáapi ÂèØ‰ª•Êìç‰Ωú elementÔºâ
      for (let i = 0; i < elements.length; i++) {
        elements[i].classList.add(className)
      }
      return this
    },
    //Ê≥®ÊÑèËøôÈáå‰ª£Á†ÅÁõ¥Êé• return ÁöÑÊÑèÊÄùÔºåÂéüÊú¨ÊòØÂ£∞Êòé‰∏Ä‰∏™ÂØπË±°ÔºåÂêçÂ≠óÊòØapiÔºåÁÑ∂ÂêéËøîÂõûËøô‰∏™ÂØπË±°„ÄÇ‰∏äËø∞‰ª£Á†ÅÁõ¥Êé•ËøîÂõûÂØπË±°Ôºå‰∏çÁî®ÂèñÂêçÂ≠ó‰∫Ü
    //ËøôÈáå api ÊòØÂØπË±°ÔºåÂÜôÊ≥ïÊòØES6Êèê‰æõÁöÑÁÆÄÂÜôÊñπÂºèÔºõÂéüËØ≠Ê≥ïÊòØÔºöconst api = {"addClass":function(className){for(...){...}}...;} ‰πüÂ∞±ÊòØÊääÂºïÂè∑ÂíåfunctionÂà†Êéâ‰∫Ü„ÄÇ
    //Èó≠ÂåÖÔºöÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÂèòÈáè„ÄÇÂú®ËøôÈáåÂ∞±ÊòØ addClass ËÆøÈóÆ‰∫Ü elements

    find(selector) {
      //Êü•Êâæ #xxx ÈáåÁöÑ .red ÂÖÉÁ¥†„ÄÇÂ£∞Êòé‰∏Ä‰∏™‰∏¥Êó∂Êï∞ÁªÑÁî®Êù•ÂÇ®Â≠òÊü•ÊâæÁöÑÂÖÉÁ¥†ÔºåÁÑ∂ÂêéÈÅçÂéÜelementsÔºåÂéªÊâæÊÉ≥Ë¶ÅÁöÑÂÖÉÁ¥†
      let array = []
      for (let i = 0; i < elements.length; i++) {
        const element2 = Array.from(elements[i].querySelectorAll(selector))
        array = array.concat(element2)
        //Áî®‰πãÂâçÁöÑÁ©∫Êï∞ÁªÑËøûÊé•‰∏äÊñ∞ÁöÑÂÖÉÁ¥†ÔºåÊääËøûÊé•‰∏äÊñ∞ÂÖÉÁ¥†ÁöÑÊï∞ÁªÑÊîæÂõûÂà∞array„ÄÇÔºàÂæóÂà∞ÁöÑÊòØ‰º™Êï∞ÁªÑÔºåÈúÄË¶ÅÁî®Array.fromËΩ¨ÂåñÊàêÊï∞ÁªÑÂÜçÊîæÂõûÔºâ
      }
      array.oldApi = this //thisÊòØÊóßÁöÑapi
      return jQuery(array) //ÊòØ const newApi = jQuery(array);ÁÑ∂Âêéreturn newApi;ÁöÑÁÆÄÂÜô
    },

    oldApi: selectorOrArray.oldApi,
    //Âä†ËøôÂè•ËØùÊòØÂõ†‰∏∫ÔºåÂâçÈù¢ÊääoldApiÊîæÂú®‰∫Üarray‰∏äÈù¢ÔºåÂπ∂‰∏çÊòØÂú®api‰∏äÔºàapiÊòØÊìç‰ΩúÊï∞ÁªÑÁöÑÔºâÔºåÊâÄ‰ª•ÈúÄË¶ÅÊèí‰∏äËøô‰∏™Êìç‰Ωú
    end() {
      //Êü•Êâæ #xxx ÈáåÁöÑ .red ÂÖÉÁ¥†ÂêéËøîÂõû‰∏ä‰∏ÄÁ∫ß
      return this.oldApi //thisÊòØÊñ∞ÁöÑapiÔºàÂèØ‰ª•Áúã‰∏Ä‰∏ãmain.js‰∏≠Áõ∏ÂÖ≥ÁöÑ‰ª£Á†ÅÁêÜËß£Ôºâ
    },

    each(fn) {
      for (let i = 0; i < elements.length; i++) {
        fn.call(null, elements[i], i)
      }
      return this //Ëøô‰∏™thisÊòØapiÂØπË±°
    }, //ÈÅçÂéÜelementsÔºåÊØè‰∏™elementsË∞ÉÁî®fnÔºåthisÊòØÁ©∫ÔºåÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞ÊòØelements[i]ÔºåÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØi
    parent() {
      const array = []
      this.each((node) => {
        if (array.indexOf(node.parentNode) === -1) {
          //ËøôÈáå=== -1 ÂÖ∂ÂÆûÊòØ<=0‰∏™ÁöÑÊÑèÊÄùÔºåÂç≥Â¶ÇÊûúÊ≤°ÊúâpushÂ∞±push‰∏Ä‰∏ãÔºåpushËøá‰∫ÜÂ∞±‰∏çpush‰∫ÜÔºåÈÅøÂÖçÈáçÂ§ç
          array.push(node.parentNode)
        }
      })
      return jQuery(array)
    }, //Ê£ÄÊü•ÂÖ∂Áà∏Áà∏Êó∂ÔºåÂ¶ÇÊûúÊúâÂ§ö‰∏™ÂÖÉÁ¥†ÂØπÂ∫î‰∏Ä‰∏™Áà∏Áà∏ÔºåÂÆÉ‰ºöÈáçÂ§çÊâìÂç∞Âá∫Êù•ÔºåÂÆûÈôÖÊàë‰ª¨Âè™ÈúÄË¶Å‰∏Ä‰∏™ÔºåÊâÄ‰ª•Âä†‰∏Ä‰∏™Âà§Êñ≠Êù°‰ª∂„ÄÇ
    children() {
      const array = []
      this.each((node) => {
        array.push(...node.children) //...ÊòØÊåáÂ±ïÂºÄÊìç‰ΩúÁ¨¶ÔºåÂ∞±ÊòØÊääÊï∞ÁªÑÂ±ïÂºÄ‰Ωú‰∏∫Á¨¨‰∏Ä‰∫å‰∏â...‰∏™ÂèÇÊï∞Â±ïÁ§∫
      })
      return jQuery(array)
    },
    print() {
      console.log(elements)
    }, //Áî®print()Êù•Ë°®Á§∫ÊâìÂç∞„ÄÇ
  }
}

jQuery.prototype = {
  //‰ΩøÁî®ÂéüÂûãÊääÂÖ±Áî®Â±ûÊÄßÔºàÂáΩÊï∞ÔºâÂÖ®ÈÉΩÊîæÂà∞ $.prototype
  constructor: jQuery, //jQuery.prototype ‰∏ÄÂºÄÂßãÂ∞±Êúâ constructor Â±ûÊÄßÔºå‰∏çÂÜôËøôÂè•ËØù‰ºöË¢´Ë¶ÜÁõñ
}
